.radio__label {
  display: inline-block;
  position: relative;
  cursor: pointer;
  padding: 7px 0 18px 45px;
  hyphens: auto;
  color: #444444;
  font-family: "Oswaldregular", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  text-transform: uppercase;
}
.radio__label::before {
  content: "";
  width: 29px;
  height: 29px;
  position: absolute;
  border-radius: 50%;
  top: 0px;
  left: -1px;
  border: 1px solid #cdcdcd;
  background-color: transparent;
}
.radio__label::after {
  content: "";
  position: absolute;
  border-radius: 50%;
  display: none;
  background: #68b738;
  width: 14px;
  height: 14px;
  left: 8px;
  top: 8px;
}
.radio__input:checked + .radio__label::before {
  border: 1px solid #cdcdcd;
}
.radio__input:checked + .radio__label::after {
  display: block;
}
.radio__input:hover + .radio__label::before {
  border: 1px solid #bababa;
}
.radio__input:focus + .radio__label::before {
  border: 1px solid #bababa;
}
.radio__input:disabled + .radio__label {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .radio__label {
    display: inline-block;
    position: relative;
    cursor: pointer;
    padding: 6px 0 17px 86px;
    hyphens: auto;
    font-size: 20px;
    font-weight: 400;
    line-height: 30px;
  }
  .radio__label::before {
    content: "";
    width: 38px;
    height: 38px;
    position: absolute;
    border-radius: 50%;
    top: 0;
    left: 26px;
    border: 1px solid #cdcdcd;
    background-color: transparent;
  }
  .radio__label::after {
    content: "";
    position: absolute;
    border-radius: 50%;
    display: none;
    background: #68b738;
    width: 18px;
    height: 18px;
    left: 37px;
    top: 11px;
  }
}
.checkbox-label {
  display: inline-block;
  position: relative;
  cursor: pointer;
  hyphens: auto;
  padding: 8px 40px 16px 45px;
  color: #444444;
  font-family: "Oswaldregular", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  text-transform: uppercase;
}
.checkbox-label::before {
  content: "";
  width: 28px;
  height: 28px;
  position: absolute;
  top: 3px;
  left: 0;
  border: 1px solid #cdcdcd;
  background-color: transparent;
}
.checkbox-label::after {
  content: "";
  width: 15px;
  height: 8px;
  position: absolute;
  top: 9px;
  left: 7px;
  border-bottom: 2px solid #68b738;
  border-left: 2px solid #68b738;
  transform: rotate(-45deg);
  display: none;
}
.checkbox__input:checked + .checkbox-label::before {
  border: 1px solid #cdcdcd;
}
.checkbox__input:checked + .checkbox-label::after {
  display: block;
}
.checkbox__input:hover + .checkbox-label::before {
  border: 1px solid #bababa;
}
.checkbox__input:focus + .checkbox-label::before {
  border: 1px solid #bababa;
}
.checkbox__input:disabled + .checkbox-label {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .checkbox-label {
    padding: 9px 40px 16px 60px;
    font-size: 20px;
    font-weight: 400;
    line-height: 30px;
  }
  .checkbox-label::before {
    width: 38px;
    height: 38px;
    top: 3px;
    left: 0;
  }
  .checkbox-label::after {
    width: 20px;
    height: 10px;
    top: 14px;
